body{
    padding: 0;
    margin: 0;
    max-width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size:cover;
    background-attachment:fixed;
    background-repeat:no-repeat;
  }
  
  .jumbotron {
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
      "Lucida Sans", Arial, sans-serif;
    background-color: #000000;
    color: #ffffff;
    margin: auto;
    height:14rem;
    text-align: center;
    background-repeat:no-repeat ;
  }
  .jumbotron h1 {
    display: inline-block;
    font-size: 60px;
  }
  .jumbotron h1 .dreami {
    color: #ff1493;
  }
  
  .bold {
    font-weight: bold;
  }
  
  .container {
    margin-top: 50px;
  }
  
  .footer {
    margin-top: 20px;
    padding-top: 20px;
  }